import React from 'react';
import Checkbox from '../Checkbox/Checkbox';
import styles from './ChannelList.module.scss';

export default class ChannelList extends React.Component {

    renderChannelList = () => {
        const update = this.props.update || {}
        const { graphItem, selectedChannel } = update

        if (!graphItem) return null
        if (graphItem.channelsY1.length === 0 && graphItem.channelsY2.length === 0) {
            return <div className={styles.empty}>Channel is empty.</div>
        }

        let channelList = [];
        if (graphItem.channelsY1.length > 0) {
            channelList.push(<div className={styles.label} key={-1}>Left Y axis:</div>)
            graphItem.channelsY1.forEach(channelId => {
                let channel = selectedChannel.find(c => c.id === channelId)
                if (!channel) return
                channelList.push(this.createChannelItem(graphItem?.id, channel, "channelsY1"))
            })
        }
        if (graphItem.channelsY2.length > 0) {
            channelList.push(<div className={styles.label} key={-2}>Right Y axis:</div>)
            graphItem.channelsY2.forEach(channelId => {
                let channel = selectedChannel.find(c => c.id === channelId)
                if (!channel) return
                channelList.push(this.createChannelItem(graphItem?.id, channel, "channelsY2"))
            })
        }
        return channelList

    }

    createChannelItem = (graphId, channel, channelYType) => {
        const htmlId = graphId + "-" + channel.id
        return (
            <Checkbox
                key={htmlId}
                className={styles.checkbox}
                label={channel.name}
                id={htmlId}
                labelColor={channel.color}
                defaultChecked={true}
                onChange={(e) => this.onSelectChannel(e, channel, graphId, channelYType)}
            />
        )
    }

    onSelectChannel = (e, channel, graphId, channelYType) => {
        const update = this.props.update || {}
        const { updateGraphChannels } = update
        const checked = e.target.checked
        if (!checked) {
            updateGraphChannels && updateGraphChannels("uncheck", { graphId, channelId: channel.id, channelYType })
        }
    }

    render() {
        return (
            <div className={styles.container}>
                {this.renderChannelList()}
            </div>
        )
    }
}