import React from 'react';
import Dropdown from '../Dropdown/Dropdown.js';
import styles from './Tooltip.module.scss';

const capitalizeFirstLetter = (string) => string.charAt(0).toUpperCase() + string.slice(1);
export default class Tooltip extends React.Component {
    state = {
        showDropdown: false
    }

    onClick = () => {
        const { onClick, dropdown } = this.props
        onClick && onClick()
        if (dropdown) {
            this.setState({ showDropdown: !this.state.showDropdown })
        }
    }

    render() {
        const { text, placement, dragging, badge, count, disabled, className, dropdownData } = this.props;
        return (
            <div
                className={`${styles.container} ${disabled ? styles.disabled : ""} ${className || ""}`}
                draggable={dragging}
            >
                <span className={`${styles.tooltipText} ${styles[`tooltip${capitalizeFirstLetter(placement || "bottom")}`]}`}>{text}</span>
                {badge && !!count && <span className={styles.notiBadge}>{count}</span>}
                <div className={styles.children} onClick={this.onClick}>
                    {this.props.children}
                </div>

                {
                    this.state.showDropdown &&
                    <div className={styles.dropdown}>
                        <Dropdown dropdownData={dropdownData} />
                    </div>
                }
            </div>
        )
    }
}