function getTime(inTime) {
    if (inTime instanceof Object) {
        return inTime.getTime();
    }

    return inTime;
}

function ChannelData(_name, _color) {
    this.name = _name;
    this.hexColor = _color;
    var rgbVal = RGBvalues.color(_color);
    this.color = [rgbVal.r / 255.0, rgbVal.g / 255.0, rgbVal.b / 255.0, 1.0];
    this.minMaxColor = [rgbVal.r / 255.0, rgbVal.g / 255.0, rgbVal.b / 255.0, 0.55];
    this.values = [];
    this.minValues = [];
    this.maxValues = [];
    this.highlightCoord = null;
    this.highlightBufferId = 0;
    this.highlightLineColor = [1, 0.1, 0.1, 0.8];
    this.highlightLineCoord = null;
    this.highlightLineBufferId = 0;
    this.dataSize = 0;
    this.bufferList = [];
    this.bufferIdList = [];
    this.bufferLenList = [];
    this.isHaveMinMaxBuffer = false;
    this.minMaxBufferList = [];
    this.minMaxBufferIdList = [];
    this.minMaxBufferLenList = [];
    this.minY = null;
    this.maxY = null;
    this.filterBadData = false;

    this.isAggregated = false;
    this.isLine = false;
    this.aggregatedTimes = [];

    this.timeShift = 0.0;
    this.pointSize = 15.0;

    this.aggregatedFileDataLengthList = [];
}

ChannelData.prototype.setValues = function (_values, _minValues, _maxValues, _dataSize) {
    this.values = _values;
    this.minValues = _minValues;
    this.maxValues = _maxValues;
    if (this.minValues != null && this.maxValues != null) {
        if (this.minValues.length > 0 && this.maxValues.length > 0) {
            this.isHaveMinMaxBuffer = true;
        }
    }
    this.dataSize = _dataSize;
}

ChannelData.prototype.updateMinMaxY = function (_times) {
    this.minY = null;
    this.maxY = null;
    var x, y;
    var arraySize = this.values.length;
    for (var i = 0; i < arraySize; i++) {
        if (this.isAggregated) {
            x = this.aggregatedTimes[i];
        } else {
            x = _times[i];
        }
        y = this.values[i];
        if (x != null && !isNaN(x) && x > 0 && y != null && !isNaN(y)) {
            if (this.filterBadData && Math.abs(y) > 9e5) {
                continue;
            }

            if (!this.isHaveMinMaxBuffer) {
                if (this.minY == null || y < this.minY) {
                    this.minY = y;
                }

                if (this.maxY == null || y > this.maxY) {
                    this.maxY = y;
                }
            } else {
                var miY = this.minValues[i];
                var maY = this.maxValues[i];

                if (this.minY == null || miY < this.minY) {
                    this.minY = miY;
                }

                if (this.maxY == null || maY > this.maxY) {
                    this.maxY = maY;
                }
            }
        }
    }

    //console.log("min,maxY",this.minY,this.maxY);
}

ChannelData.prototype.updateBuffers = function (_gl, _convertedTimes, _minX, _maxX, _minY, _maxY, pointShapeType) {
    if (this.isAggregated) {
        var arrayLen = this.aggregatedTimes.length;
        var fileNum = 0;
        var fileSize = 0;
        for (var i = 0; i < arrayLen; i++) {
            if ((!this.aggregatedTimes[i] || this.aggregatedTimes[i] < 0) && fileSize > 0) {
                this.bufferLenList.push(fileSize);
                if (this.isHaveMinMaxBuffer) {
                    this.minMaxBufferLenList.push(fileSize * 2);
                }
                fileSize = 0;
                fileNum++;
            } else {
                fileSize++;
            }
        }


        if (fileSize > 0) {
            this.bufferLenList.push(fileSize);
            if (this.isHaveMinMaxBuffer) {
                this.minMaxBufferLenList.push(fileSize * 2);
            }
            fileNum++;
        }

        let pos = 0;
        var rangeX = _maxX - _minX;
        var rangeY = _maxY - _minY;
        if (rangeY < 1.e-10) rangeY = 1.0;
        for (var i = 0; i < fileNum; i++) {
            while (pos < arrayLen && (this.aggregatedTimes[pos] < 0 || !this.aggregatedTimes[pos])) {
                pos++;
            }
            if (pos >= arrayLen) {
                break;
            }

            var fileSize = this.bufferLenList[i];
            var buffer = new Float32Array(fileSize * 2);
            var minMaxBuffer = null;
            if (this.isHaveMinMaxBuffer) {
                minMaxBuffer = new Float32Array(fileSize * 4);
            }
            var validSize = 0;
            for (var j = pos; j < pos + fileSize; j++) {
                var t = this.aggregatedTimes[j] + this.timeShift;
                var x = -1.0;
                if (t != null && !isNaN(t) && t > 0) {
                    x = (t - _minX) / rangeX;
                }
                var y = this.values[j];

                buffer[validSize * 2] = x;
                buffer[validSize * 2 + 1] = (y - _minY) / rangeY;

                if (this.isHaveMinMaxBuffer) {
                    minMaxBuffer[validSize * 4] = buffer[validSize * 2];
                    minMaxBuffer[validSize * 4 + 1] = (miY - _minY) / rangeY;

                    minMaxBuffer[validSize * 4 + 2] = buffer[validSize * 2];
                    minMaxBuffer[validSize * 4 + 3] = (maY - _minY) / rangeY;
                }

                validSize++;
            }

            pos += fileSize;

            if (validSize < fileSize) {
                buffer = buffer.subarray(0, validSize * 2);
                if (this.isHaveMinMaxBuffer) {
                    minMaxBuffer = minMaxBuffer.subarray(0, validSize * 4);
                }
            }

            this.bufferList.push(buffer);
            if (this.isHaveMinMaxBuffer) {
                this.minMaxBufferList.push(minMaxBuffer);
            }

            this.bufferLenList[i] = validSize;
            var bufferId = _gl.createBuffer();
            _gl.bindBuffer(_gl.ARRAY_BUFFER, bufferId);
            _gl.bufferData(_gl.ARRAY_BUFFER, buffer, _gl.STATIC_DRAW);
            this.bufferIdList.push(bufferId);

            if (this.isHaveMinMaxBuffer) {
                this.minMaxBufferLenList[i] = validSize * 2;

                var minMaxBufferId = _gl.createBuffer();
                _gl.bindBuffer(_gl.ARRAY_BUFFER, minMaxBufferId);
                _gl.bufferData(_gl.ARRAY_BUFFER, minMaxBuffer, _gl.STATIC_DRAW);
                this.minMaxBufferIdList.push(minMaxBufferId);
            }
        }
    } else {
        var arrayLen = _convertedTimes.length;
        var fileNum = 0;
        var fileSize = 0;
        for (var i = 0; i < arrayLen; i++) {
            if (_convertedTimes[i] < 0 && fileSize > 0) {
                this.bufferLenList.push(fileSize);
                if (this.isHaveMinMaxBuffer) {
                    this.minMaxBufferLenList.push(fileSize * 2);
                }
                fileSize = 0;
                fileNum++;
            } else {
                fileSize++;
            }
        }

        if (fileSize > 0) {
            this.bufferLenList.push(fileSize);
            if (this.isHaveMinMaxBuffer) {
                this.minMaxBufferLenList.push(fileSize * 2);
            }
            fileNum++;
        }

        let pos = 0;
        var rangeX = _maxX - _minX;
        var rangeY = _maxY - _minY;
        if (rangeY < 1.e-10) rangeY = 1.0;
        for (var i = 0; i < fileNum; i++) {
            while (pos < arrayLen && _convertedTimes[pos] < 0) {
                pos++;
            }
            if (pos >= arrayLen) {
                break;
            }

            var fileSize = this.bufferLenList[i];
            var buffer = new Float32Array(fileSize * 2);
            var minMaxBuffer = null;
            if (this.isHaveMinMaxBuffer) {
                minMaxBuffer = new Float32Array(fileSize * 4);
            }
            var validSize = 0;
            var preY = null;
            // bad data is head file
            for (var j = pos; j < pos + fileSize; j++) {
                if (Math.abs(this.values[j]) <= 9e5) {
                    preY = this.values[j];
                    break;
                }
            }
            for (var j = pos; j < pos + fileSize; j++) {
                var x = _convertedTimes[j] + this.timeShift / rangeX;
                var y = this.values[j];
                if (y != null && !isNaN(y)) {
                    var miY, maY;
                    if (this.isHaveMinMaxBuffer) {
                        miY = this.minValues[j];
                        maY = this.maxValues[j];
                    }
                    if (this.filterBadData) {
                        if (Math.abs(y) > 9e5) {
                            y = preY;
                            miY = preY;
                            maY = preY;
                        }
                        preY = y;
                    }
                    buffer[validSize * 2] = x;
                    buffer[validSize * 2 + 1] = (y - _minY) / rangeY;

                    if (this.isHaveMinMaxBuffer) {
                        minMaxBuffer[validSize * 4] = buffer[validSize * 2];
                        minMaxBuffer[validSize * 4 + 1] = (miY - _minY) / rangeY;

                        minMaxBuffer[validSize * 4 + 2] = buffer[validSize * 2];
                        minMaxBuffer[validSize * 4 + 3] = (maY - _minY) / rangeY;
                    }

                    validSize++;
                }
            }

            pos += fileSize;

            if (validSize < fileSize) {
                buffer = buffer.subarray(0, validSize * 2);
                if (this.isHaveMinMaxBuffer) {
                    minMaxBuffer = minMaxBuffer.subarray(0, validSize * 4);
                }
            }

            this.bufferList.push(buffer);
            if (this.isHaveMinMaxBuffer) {
                this.minMaxBufferList.push(minMaxBuffer);
            }

            this.bufferLenList[i] = validSize;

            var bufferId = _gl.createBuffer();
            _gl.bindBuffer(_gl.ARRAY_BUFFER, bufferId);
            _gl.bufferData(_gl.ARRAY_BUFFER, buffer, _gl.STATIC_DRAW);
            this.bufferIdList.push(bufferId);

            if (this.isHaveMinMaxBuffer) {
                this.minMaxBufferLenList[i] = validSize * 2;

                var minMaxBufferId = _gl.createBuffer();
                _gl.bindBuffer(_gl.ARRAY_BUFFER, minMaxBufferId);
                _gl.bufferData(_gl.ARRAY_BUFFER, minMaxBuffer, _gl.STATIC_DRAW);
                this.minMaxBufferIdList.push(minMaxBufferId);
            }
        }
    }
}

ChannelData.prototype.updateHighlightBuffer = function (_gl, timeValue, _timeIdx, _plotMinX, _plotMaxX, _plotMinY, _plotMaxY) {
    var xVal = timeValue;
    var yVal = this.values[_timeIdx];

    if (this.highlightBufferId == 0) {
        this.highlightCoord = new Float32Array(2);
        this.highlightBufferId = _gl.createBuffer();
    }

    if (this.highlightLineBufferId == 0) {
        this.highlightLineCoord = new Float32Array(4);
        this.highlightLineBufferId = _gl.createBuffer();
    }

    this.highlightCoord[0] = (xVal - _plotMinX) / (_plotMaxX - _plotMinX);
    this.highlightCoord[1] = (yVal - _plotMinY) / (_plotMaxY - _plotMinY);

    this.highlightLineCoord[0] = (xVal - _plotMinX) / (_plotMaxX - _plotMinX);
    this.highlightLineCoord[1] = -1.0e16;
    this.highlightLineCoord[2] = (xVal - _plotMinX) / (_plotMaxX - _plotMinX);
    this.highlightLineCoord[3] = 2.0e16;

    _gl.bindBuffer(_gl.ARRAY_BUFFER, this.highlightBufferId);
    _gl.bufferData(_gl.ARRAY_BUFFER, this.highlightCoord, _gl.STATIC_DRAW);

    _gl.bindBuffer(_gl.ARRAY_BUFFER, this.highlightLineBufferId);
    _gl.bufferData(_gl.ARRAY_BUFFER, this.highlightLineCoord, _gl.STATIC_DRAW);
}

