import React from 'react';
import styles from "./Checkbox.module.scss"

export default class Checkbox extends React.Component {

    render() {
        const { defaultChecked, checked, label, labelColor, onChange, disabled, id, className } = this.props
        return (
            <div className={`${styles.container} ${disabled ? styles.disabled : ""} ${className || ""}`}>
                <input
                    type="checkbox"
                    className="checkbox-comp"
                    id={id}
                    value={checked}
                    onChange={onChange}
                    defaultChecked={defaultChecked}
                    disabled={disabled} />
                <span></span>
                <label htmlFor={id} style={{ color: labelColor }}>{label}</label>
            </div>
        )
    }
}