import React from "react";
import styles from "./Graph.module.scss";
import { _doubleClick, _mouseDown, _mouseScroll, _mouseUp } from "../utils";
import { _mouseMove, _mouseClick } from './../utils';

const Dygraph = window.Dygraph;

const MIN = 40;
const GRAPH_X_LABEL_WIDTH = 34;
const GRAPH_Y_LABEL_WIDTH = 60;

export default class Graph extends React.Component {
    mouseScrollTimer = null;
    _isMouseDown = false;
    _isMouseMove = false;
    eventMouse = null;
    state = {
        graph: null,
        popupData: {}
    }

    shouldComponentUpdate(nextProps, nextState) {
        if (this.props.graphItem.id !== nextProps.graphItem.id
            || this.props.graphItem.channelsY1 !== nextProps.graphItem.channelsY1
            || this.props.graphItem.channelsY2 !== nextProps.graphItem.channelsY2
            || this.props.graphItem.height !== nextProps.graphItem.height
            || this.props.isLast !== nextProps.isLast
            || this.props.selectedChannel !== nextProps.selectedChannel
            || this.props.selectedFile !== nextProps.selectedFile
            || this.props.forceDisplayY2 !== nextProps.forceDisplayY2
            || this.props.needReduce !== nextProps.needReduce
            || this.props.thresholds !== nextProps.thresholds
            || !!this.state.graph !== !!nextState.graph
            || this.state.popupData !== nextState.popupData) return true
        return false
    }

    graphMouseWheel = (event, g, context) => {
        event.preventDefault();
        // Limit max zoom for 2 days and min zoom for 1s
        let gRange = g.xAxisRange();
        let delta = gRange[1] - gRange[0];
        const maxDays = 30;

        // Todo uncomment to reduce data
        let maxRange = maxDays * 24 * 60 * 60 * 1000;

        if (event.deltaY > 0) {
            if (delta > maxRange) return;
        }
        else if ((delta) < 1000) return;

        // Trick to prevent scroll to fast, raise a bug
        this.mouseScrollTimer && clearTimeout(this.mouseScrollTimer);
        this.mouseScrollTimer = setTimeout(function () {
            _mouseScroll(event, g, context, 30, maxRange);
        }, 1);
    }

    componentDidMount() {
        const defaultOptions = {
            labelsDiv: this.labelNode,
            labelsSeparateLines: true,
            showLabelsOnHighlight: true,
            animatedZooms: true,
            connectSeparatedPoints: false,
            strokeWidth: 1,
            strokeBorderWidth: 0,
            highlightSeriesBackgroundAlpha: 1,
            highlightSeriesOpts: {},
            axes: {
                y: {
                    axisLabelWidth: GRAPH_Y_LABEL_WIDTH
                },
                x: {
                    axisLabelWidth: GRAPH_X_LABEL_WIDTH
                }
            },
            interactionModel: {
                mousedown: _mouseDown,
                mousemove: _mouseMove,
                click: _mouseClick,
                dblclick: _doubleClick,
                mouseup: _mouseUp,
                mousewheel: this.graphMouseWheel
            },
            labels: ["Time", "y"],
            useGLGraph: true,
            pointShapeType: 2,
            glGraphTransparent: false,
            pannableY: true,
            highlightCallback: null,
            unhighlightCallback: null,
            drawCallback: null,
            pointClickCallback: this.showPopupDetail
        }

        const graph = new Dygraph(
            this.node,
            [[new Date(), null]],
            defaultOptions
        )
        this.setState({ graph });
        this.props.onUpdate(this.props.graphItem?.id, graph)

        const resizeObserver = new ResizeObserver((entries) => {
            if (this._isMouseDown && this._isMouseMove) return
            for (const entry of entries) {
                const width = entry.contentRect.width;
                this.resize({ width })
            }
        });
        resizeObserver.observe(this.dom);
        document.addEventListener("mousedown", this.onMouseDown)
    }

    componentWillUnmount() {
        document.removeEventListener("mousedown", this.onMouseDown)
    }

    resize = ({ width, height }) => {
        const graph = this.state.graph;
        if (!graph) return
        const graphItem = this.props.graphItem;
        const h = height ? height : graphItem.height;
        width = width ? width : Math.round(this.dom?.getBoundingClientRect()?.width) + Math.random(0, 0);
        if (width && h) {
            const axes = this.props.isLast ? { x: { drawAxis: true, axisTickSize: 10 } } : { x: { drawAxis: false } }
            graph.updateOptions({ axes })
            graph.resize(width, h)
        }
    }

    getGraphData = (
        channelIds,
        channelItems,
        fileIds
    ) => {
        let colorList = [];
        let channelLabels = [];
        let yTitle = "";
        let channelData = [];
        let shapeList = {}
        let aceData = this.props.aceData;

        for (const channelId of channelIds) {
            const channel = channelItems.find(c => c.id === channelId);
            if (!channel) {
                if (process.env.NODE_ENV === "development") {
                    console.error("Channel not found.", channelId, channelIds);
                }
                return null;
            }
            if (!!channel.color) colorList.push(channel.color);

            const unit = channel.unit || "";
            if (yTitle === "") {
                yTitle = unit;
            }
            else if (yTitle !== unit && unit !== "") {
                yTitle = "Mixed Units"
            }

            const channelName = channel.name;
            channelLabels.push(channelName.replace(`:external: `, ""));
            let cData = [];
            for (const fileId of fileIds) {
                if (aceData?.[fileId] && aceData?.[fileId]?.[channelName]) {
                    cData = cData.concat(aceData[fileId][channelName]);
                }
            }
            channelData.push({ [channelName]: cData });
            shapeList[channelName] = {
                isLine: channel.id % 2 === 0,
                pointSize: 10.0,
                highlightPointSize: 12.0,
                pointShapeType: channel.id % 7
            }
        };

        return [colorList, yTitle, channelLabels, channelData, shapeList]
    }

    componentDidUpdate(prevProps) {
        if (!this.state.graph) return
        const graphItem = this.props.graphItem;
        const selectedChannel = this.props.selectedChannel;
        const forceDisplayY2 = this.props.forceDisplayY2;
        const selectedFile = this.props.selectedFile;
        const exceptionData = this.props.exceptionData;

        const dataY1 = this.getGraphData(
            graphItem.channelsY1,
            selectedChannel,
            selectedFile?.original);

        if (!dataY1) return;
        let [colorList, yTitle, channelLabels, channelData, shapeList] = dataY1;
        channelLabels = ["Time"].concat(channelLabels);

        let y2Options = {
            series: null,
            y2label: null
        };
        if (graphItem.channelsY2.length > 0) {
            const dataY2 = this.getGraphData(graphItem.channelsY2, selectedChannel, selectedFile?.original);
            if (!dataY2) return;
            let [colorListY2, y2Title, channelLabelsY2, channelDataY2, shapeList2] = dataY2;

            colorList = colorList.concat(colorListY2);
            shapeList = {
                ...shapeList,
                ...shapeList2
            }
            channelLabels = channelLabels.concat(channelLabelsY2);
            channelData = channelData.concat(channelDataY2);

            const y2Series = channelLabelsY2.reduce((o, i) => ({ ...o, [i]: { axis: "y2" } }), {})
            y2Options = { series: y2Series, y2label: y2Title }
        }

        const axes = this.props.isLast ? { x: { drawAxis: true, axisTickSize: 10 } } : { x: { drawAxis: false } }
        const range = this.state.graph.xAxisRange();

        const textMappingState = graphItem.channelsY1.concat(graphItem.channelsY2).reduce((o, id) => {
            let channel = selectedChannel.find(c => c.id === id);
            if (!channel || !channel.textMapping) return { ...o };
            return {
                ...o,
                [channel.name]: channel.textMapping
            }
        }, {})

        const options = {
            file: [],
            aggregatedData: channelData,
            exceptionData: exceptionData,
            shapes: shapeList,
            ylabel: yTitle,
            colors: colorList,
            labels: channelLabels,
            textMapping: textMappingState,
            forceDisplayY2: forceDisplayY2,
            errorBars: this.props.needReduce,
            dateWindow: !range[0] ? this.props.selectedRange : range,
            axes,
            pointClickCallback: this.showPopupDetail,
            ...y2Options
        }
        if (selectedChannel.length === 1 && this.props.thresholds) {
            let threshold = this.props.thresholds[selectedChannel[0]["name"]];
            options["threshold"] = threshold;
        }
        const _forceDisplayY2 = this.state.graph.user_attrs_?.forceDisplayY2;
        this.state.graph.updateOptions(options);

        if (forceDisplayY2 !== _forceDisplayY2 || prevProps.graphItem?.height !== this.state.graph.height) {
            this.resize({})
        }
    }

    onContextMenu = (event) => {
        event.preventDefault();
        event.stopPropagation();
    }

    animate = (e, startY, _height) => {
        const { clientY } = this.eventMouse;
        const deltaY = clientY - startY;
        const __height = _height + deltaY
        _height = __height
        _height = _height < MIN ? MIN : _height;

        if (_height > MIN) this.resize({ height: _height })
        if (__height < MIN) {
            const rect = this.resizeNode.getBoundingClientRect()
            startY = rect.y;
        } else {
            startY = clientY;
        }

        if (this._isMouseDown && this._isMouseMove) {
            window.requestAnimationFrame(() => this.animate(e, startY, _height))
        }
    }

    onResize = (e) => {
        let { clientY: startY } = e;
        const graphData = this.props.graphItem;
        let _height = graphData.height || 270;
        this._isMouseDown = true

        const onMove = (e) => {
            this.eventMouse = e;
            this.node.style.pointerEvents = "none";
            if (!this._isMouseDown) return
            e.stopImmediatePropagation()
            if (!this._isMouseMove) {
                this._isMouseMove = true;
                this.animate(e, startY, _height);
            }
        }
        const onUp = (e) => {
            this.node.removeAttribute("style");
            const r = this.dom.getBoundingClientRect();
            this.props.onResize(graphData.id, r.height);
            document.removeEventListener("mousemove", onMove)
            document.removeEventListener("mouseup", onUp);
            if (!this._isMouseDown || !this._isMouseMove) return;
            // resize data chart
            this._isMouseDown = false;
            this._isMouseMove = false;
            this.eventMouse = null;
        }
        document.addEventListener("mousemove", onMove)
        document.addEventListener("mouseup", onUp)
    }

    showPopupDetail = (event, data) => {
        event.preventDefault()
        this.setState({
            popupData: {
                x: event.pageX,
                y: event.pageY,
                data: data?.point,
                show: true
            }
        })
    }

    onMouseDown = (event) => {
        if (event.target.closest(".popup-detail-graph") && event.target.closest(".popup-detail-graph").isSameNode(this.popup)) {
            return
        }

        if (this.state.popupData.show) {
            this.setState({ popupData: {} })
        }
    }


    render() {
        const { popupData } = this.state
        return <div className={styles.container}
            ref={node => this.dom = node}
            onContextMenu={this.onContextMenu}>
            <div ref={node => this.node = node}></div>
            <div className={styles.legend}
                ref={label => this.labelNode = label}>
            </div>
            {!this.props.isLast &&
                <div ref={n => this.resizeNode = n}
                    className={styles.gutter}
                    onMouseDown={this.onResize}>
                </div>}
            {popupData?.show && popupData?.data &&
                <PopupDetailGraph
                    setRef={ref => this.popup = ref}
                    {...popupData}
                />}
        </div>
    }
}

const PopupDetailGraph = ({ x, y, data, setRef }) => {
    const xData = new Date(data?.[0])?.toDateString()
    const yData = data?.[1] || ""
    const graphContainerRect = document.querySelector(".mf-graph-container")?.getBoundingClientRect() || {}
    const top = y + graphContainerRect?.y + 12
    return <div
        ref={setRef}
        className={`${styles.popup} popup-detail-graph ${y + 66 > graphContainerRect?.height - graphContainerRect.top ? styles.top : ""}`}
        style={{ left: x - 20, top: y + 66 > graphContainerRect?.height - graphContainerRect.top ? top - 112 : top }}>
        <div title={xData}><b>X:</b> {xData}</div>
        <div title={yData}><b>Y:</b> {yData}</div>
    </div>
}