import React from "react";
import GraphPanel from "../GraphPanel/GraphPanel";
import Switch from "../Switch/Switch";
import Tooltip from "../Tooltip/Tooltip";
import { syncGraphs } from "../utils";
import styles from "./GraphContainer.module.scss";
import "./dygraph.scss"

const DEFAULT_HEIGHT = 270;

export default class GraphContainer extends React.Component {
    state = {
        isMoving: false,
        graphList: [],
        selectedRange: [0, 0],
        graphHeight: DEFAULT_HEIGHT,
    }

    componentDidMount() {
        this.setState({ graphList: this.props.graphList, selectedRange: this.props.selectedRange })
    }

    allowDrop = (event) => {
        event.preventDefault();
        event.stopPropagation();
        if (!this.state.isMoving) {
            this.setState({ isMoving: true })
        }
    }

    onDragEnter = (event) => {
        event.preventDefault();
        event.stopPropagation();
    }

    onDragLeave = (event) => {
        event.preventDefault();
        event.stopPropagation();
        const r = this.node.getBoundingClientRect();
        const isCondition = event.clientX >= r.left
            && event.clientX <= r.left + r.width
            && event.clientY >= r.top
            && event.clientY <= r.top + r.height
        if (!isCondition) this.setState({ isMoving: false })
    }

    onDrop = (event) => {
        event.preventDefault();
        event.stopPropagation();
        this.setState({ isMoving: false });
        const arr = this.state.graphList.map(i => i.id);
        const max = arr.length === 0 ? 0 : Math.max(...arr);
        const newGraphList = this.state.graphList;
        newGraphList.push({
            id: max + 1,
            channelsY1: [this.props.channelId],
            channelsY2: [],
            height: DEFAULT_HEIGHT
        })
        this.setState({ graphList: newGraphList })

    }

    onInsert = () => {
        const arr = this.state.graphList.map(i => i.id);
        const max = Math.max(...arr);
        const newGraphList = this.state.graphList;
        newGraphList.push({
            id: max + 1,
            channelsY1: [],
            channelsY2: [],
            height: DEFAULT_HEIGHT
        })

        this.setState({ graphList: newGraphList })
    }

    onUpdate = (id, graph) => {
        const newGraphList = this.state.graphList;
        const ix = newGraphList.findIndex(i => i.id === id);
        if (ix === -1) return
        newGraphList[ix].graph = graph;
        this.setState({ graphList: newGraphList });
        const graphInstances = newGraphList.filter(i => !!i.graph).sort((a, b) => a.id - b.id).map(i => i.graph);
        syncGraphs(graphInstances);
        // Let's all graphs have the same zoom as the first graph
        if (graphInstances.length > 0) {
            graphInstances[0].updateOptions({
                dateWindow: graphInstances[0].xAxisRange()
            })
        }
    }

    onDelete = (id) => {
        const newGraphList = this.state.graphList.filter(i => i.id !== id);
        this.setState({ graphList: newGraphList })
    }

    updateIsMoving = (isMoving) => {
        this.setState({ isMoving })
    }

    onInsertChannel = (id, isY2Axis) => {
        const ix = this.state.graphList.findIndex(i => i.id === id);
        const newGraphList = this.state.graphList;
        const graphItem = this.state.graphList[ix];
        const channelsY1 = isY2Axis ? graphItem.channelsY1.filter(i => i !== this.props.channelId) : graphItem.channelsY1.filter(i => i !== this.props.channelId).concat([this.props.channelId])
        const channelsY2 = !isY2Axis ? graphItem.channelsY2.filter(i => i !== this.props.channelId) : graphItem.channelsY2.filter(i => i !== this.props.channelId).concat([this.props.channelId])

        newGraphList[ix] = {
            ...newGraphList[ix],
            channelsY1,
            channelsY2
        }

        this.setState({ graphList: newGraphList })
    }

    updateGraphChannels = (option, { graphId, channelId, channelYType }) => {
        switch (option) {
            case "uncheck":
                this.unCheckGraphChannel(graphId, channelId, channelYType)
                break
            default:
                break
        }
    }

    unCheckGraphChannel = (graphId, channelId, channelYType) => {
        const { setChannelSelected } = this.props
        let newGraphList = [...this.state.graphList]

        let keepChannelSelected = newGraphList.some(graph => graph.id !== graphId && (graph.channelsY1.includes(channelId) || graph.channelsY2.includes(channelId)))
        if (!keepChannelSelected) {
            setChannelSelected && setChannelSelected({ id: channelId, seleted: false })
        }

        let graphIndex = newGraphList.findIndex(graph => graph.id === graphId)
        if (graphIndex === -1) {
            graphIndex = newGraphList.length - 1
        }
        let graph = newGraphList[graphIndex]

        newGraphList[graphIndex] = {
            ...graph,
            [channelYType]: graph?.[channelYType]?.filter(val => val !== channelId)
        }

        this.setState({ graphList: newGraphList })
    }

    handleGraphHeight = (action) => {
        let graphHeight = this.state.graphHeight || DEFAULT_HEIGHT
        const MIN_HEIGHT_GRAPH = 50
        let newGraphList = [...this.state.graphList] || []
        const gNum = newGraphList.length
        switch (action) {
            case "fit_in_screen":
                const nodeRect = this.node?.getBoundingClientRect()
                const hContainer = this.container?.getBoundingClientRect().height - nodeRect.top
                const h = (hContainer - (gNum - 1) * 8) / gNum;
                graphHeight = Math.max(MIN_HEIGHT_GRAPH, h)
                newGraphList = newGraphList.map(i => ({ ...i, height: graphHeight }))
                this.setState({ graphList: newGraphList, graphHeight })
                break
            case "increase_height":
            case "decrease_height":
                const step = action === "increase_height" ? 10 : -10;
                graphHeight = Math.max(MIN_HEIGHT_GRAPH, graphHeight + step)
                newGraphList = newGraphList.map(i => ({ ...i, height: Math.max(MIN_HEIGHT_GRAPH, (i?.height || DEFAULT_HEIGHT) + step) }))
                this.setState({ graphList: newGraphList, graphHeight })
                break
            default:
                break
        }
    }

    onResizeGraphPannel = (id, height) => {
        const newGraphList = this.state.graphList;
        const ix = newGraphList.findIndex(i => i.id === id);
        newGraphList[ix].height = height;
        this.setState({ graphList: newGraphList })
    }

    render() {
        const graphList = this.state.graphList || [];
        const forceDisplayY2 = graphList.some(g => g.channelsY2.length);
        const disableToolbar = graphList.length === 0

        return <div className={`${styles.container} ${this.state.isMoving ? styles.isMoving : ""} dygraph mf-graph-container`} ref={ref => this.container = ref}>
            <div className={styles.header}>
                <div className={styles.switch}>
                    <Switch checked={true} disabled={true} />
                    <div>Filter bad data (~1E6)</div>
                </div>

                <div className={styles.stripHeight}>
                    <div>Strip height:</div>
                    <Tooltip
                        className={styles.h}
                        text="Decrease height."
                        disabled={disableToolbar}
                        onClick={() => this.handleGraphHeight("decrease_height")}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="rgb(255, 255, 255)" viewBox="0 0 16 16"><path fillRule="evenodd" d="M2 8a.5.5 0 0 1 .5-.5h11a.5.5 0 0 1 0 1h-11A.5.5 0 0 1 2 8Z"></path></svg>
                    </Tooltip>
                    <Tooltip
                        className={styles.h}
                        text="Increase height."
                        placement="left"
                        disabled={disableToolbar}
                        onClick={() => this.handleGraphHeight("increase_height")}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="rgb(255, 255, 255)" viewBox="0 0 16 16"><path fillRule="evenodd" d="M8 2a.5.5 0 0 1 .5.5v5h5a.5.5 0 0 1 0 1h-5v5a.5.5 0 0 1-1 0v-5h-5a.5.5 0 0 1 0-1h5v-5A.5.5 0 0 1 8 2Z"></path></svg>
                    </Tooltip>
                    <Tooltip
                        className={styles.h}
                        text="Fit in screen."
                        placement="left"
                        disabled={disableToolbar}
                        onClick={() => this.handleGraphHeight("fit_in_screen")}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="rgb(255, 255, 255)" viewBox="0 0 16 16"><path fillRule="evenodd" d="M2 12.5a.5.5 0 0 1 .5-.5h11a.5.5 0 0 1 0 1h-11a.5.5 0 0 1-.5-.5zm0-3a.5.5 0 0 1 .5-.5h11a.5.5 0 0 1 0 1h-11a.5.5 0 0 1-.5-.5zm0-3a.5.5 0 0 1 .5-.5h11a.5.5 0 0 1 0 1h-11a.5.5 0 0 1-.5-.5zm0-3a.5.5 0 0 1 .5-.5h11a.5.5 0 0 1 0 1h-11a.5.5 0 0 1-.5-.5z"></path></svg>
                    </Tooltip>
                </div>
            </div>

            <div className={styles.main}
                onDragEnter={this.onDragEnter}
                onDragOver={this.allowDrop}
                onDragLeave={this.onDragLeave}
                onDrop={this.onDrop}
                ref={n => this.node = n}>
                {graphList.length === 0 ? <div className={styles.empty}>
                    <div>Select or drop a channel here to start plotting.</div>
                </div> :
                    graphList.map((graphItem, _) => <GraphPanel
                        key={graphItem.id}
                        aceData={this.props.aceData}
                        exceptionData={this.props.exceptionData}
                        graphItem={graphItem}
                        forceDisplayY2={forceDisplayY2}
                        selectedRange={this.state.selectedRange}
                        selectedFile={this.props.selectedFile}
                        selectedChannel={this.props.selectedChannel}
                        isLast={(graphList.length - 1) === _}
                        needReduce={this.props.needReduce}
                        thresholds={this.props.thresholds}
                        updateIsMoving={this.updateIsMoving}
                        onInsertChannel={this.onInsertChannel}
                        onInsert={this.onInsert}
                        onDelete={this.onDelete}
                        onUpdate={this.onUpdate}
                        onResize={this.onResizeGraphPannel}
                        updateGraphChannels={this.updateGraphChannels} />)
                }

                {this.state.isMoving && <div className={styles.dropzone}>
                    Drop to plot on new strip.
                </div>}
            </div>
        </div>
    }
}