import React from "react";
import Graph from "../Graph/Graph";
import Tooltip from "../Tooltip/Tooltip";
import styles from "./GraphPanel.module.scss";

export default class GraphPanel extends React.Component {

    onDragOver = (event) => {
        event.preventDefault();
        event.stopPropagation();
        this.dzNode.style.opacity = 1;
        const left = event.currentTarget.getBoundingClientRect().left;
        this.props.updateIsMoving(false);
        if (event.pageX - left > (window.innerWidth - left) / 2.0) {
            this.lNode.removeAttribute("style");
            this.rNode.style.backgroundColor = "antiquewhite";
        } else {
            this.rNode.removeAttribute("style");
            this.lNode.style.backgroundColor = "antiquewhite";
        }
    }

    onDrop = (event) => {
        event.preventDefault();
        event.stopPropagation();
        this.lNode.removeAttribute("style");
        this.rNode.removeAttribute("style");
        this.dzNode.removeAttribute("style");
        const left = event.currentTarget.getBoundingClientRect().left;
        const Y2Axis = event.pageX - left > (window.innerWidth - left) / 2.0
        this.props.onInsertChannel(this.props.graphItem.id, Y2Axis);
    }

    onLeave = (event) => {
        event.preventDefault();
        event.stopPropagation();
        this.lNode.removeAttribute("style");
        this.rNode.removeAttribute("style");
        this.dzNode.removeAttribute("style");
    }

    render() {
        const { graphItem, selectedChannel, updateGraphChannels } = this.props
        const payloadChart = { graphItem, selectedChannel }
        if (!this.props.graphItem) return null;
        return <div className={styles.container}
            onDragOver={this.onDragOver}
            onDrop={this.onDrop}
            onDragLeave={this.onLeave}
            ref={node => this.node = node}>
            <Graph
                needReduce={this.props.needReduce}
                thresholds={this.props.thresholds}
                isLast={this.props.isLast}
                aceData={this.props.aceData}
                exceptionData={this.props.exceptionData}
                graphItem={this.props.graphItem}
                selectedRange={this.props.selectedRange}
                forceDisplayY2={this.props.forceDisplayY2}
                selectedFile={this.props.selectedFile}
                selectedChannel={this.props.selectedChannel}
                onUpdate={this.props.onUpdate}
                onResize={this.props.onResize} />

            <div className={styles.action}>
                <Tooltip
                    text="Toggle channel list."
                    placement="top"
                    dropdown={true}
                    dropdownData={{
                        option: "channel_list",
                        ...payloadChart,
                        updateGraphChannels,
                    }}>
                    <svg xmlns="http://www.w3.org/2000/svg" fill="rgb(108, 117, 125)" viewBox="0 0 24 24" width="24" height="24"><path d="M8.75 5.5h11.5a.75.75 0 0 1 0 1.5H8.75a.75.75 0 0 1 0-1.5Zm0 6h11.5a.75.75 0 0 1 0 1.5H8.75a.75.75 0 0 1 0-1.5Zm0 6h11.5a.75.75 0 0 1 0 1.5H8.75a.75.75 0 0 1 0-1.5ZM5 12a1 1 0 1 1-2 0 1 1 0 0 1 2 0ZM4 7a1 1 0 1 1 0-2 1 1 0 0 1 0 2Zm0 12a1 1 0 1 1 0-2 1 1 0 0 1 0 2Z"></path></svg>
                </Tooltip>
                <Tooltip
                    text="Insert a new strip below."
                    placement="left"
                    onClick={this.props.onInsert}>
                    <svg xmlns="http://www.w3.org/2000/svg" fill="rgb(13, 110, 253)" viewBox="0 0 24 24" width="24" height="24"><path d="M11.75 4.5a.75.75 0 0 1 .75.75V11h5.75a.75.75 0 0 1 0 1.5H12.5v5.75a.75.75 0 0 1-1.5 0V12.5H5.25a.75.75 0 0 1 0-1.5H11V5.25a.75.75 0 0 1 .75-.75Z"></path></svg>
                </Tooltip>
                <Tooltip
                    text="Remove this strip."
                    placement="left"
                    onClick={() => this.props.onDelete(this.props.graphItem.id)}>
                    <svg xmlns="http://www.w3.org/2000/svg" fill="rgb(220, 53, 69)" viewBox="0 0 24 24" width="24" height="24"><path d="M5.72 5.72a.75.75 0 0 1 1.06 0L12 10.94l5.22-5.22a.749.749 0 0 1 1.275.326.749.749 0 0 1-.215.734L13.06 12l5.22 5.22a.749.749 0 0 1-.326 1.275.749.749 0 0 1-.734-.215L12 13.06l-5.22 5.22a.751.751 0 0 1-1.042-.018.751.751 0 0 1-.018-1.042L10.94 12 5.72 6.78a.75.75 0 0 1 0-1.06Z"></path></svg>
                </Tooltip>
            </div>

            <div className={styles.dropzone}
                ref={dz => this.dzNode = dz}>
                <div ref={l => this.lNode = l}>Drop to plot on left Y</div>
                <div ref={r => this.rNode = r}>Drop to plot on right Y</div>
            </div>
        </div>
    }
}