/** Convert datetime to julian number
 * _datetime must be local time
 * Return julian theo UTC time
 * @param {Date} datetime input datetime
 */
export const datetimeToJulian = (_datetime) => {
    let datetime = getUTCTimestamp(_datetime);
    let baseDate = -6857308800000; // getUTCTimestamp("1752-09-13 00:00:00")
    let delta = datetime - baseDate;
    return delta / 86400000.0;
    // return x + y
}
/** Convert julian number to datetime
 * @param {number} julian_date julian number
 * Return local time
 */
export const julianToDatetime = (julian_date) => {
    var jul;
    if (typeof julian_date === "string") {
        jul = JSON.parse(julian_date);
    } else {
        jul = julian_date
    }
    let baseDate = -6857308800000; // getUTCTimestamp("1752-09-13 00:00:00")
    let ret = new Date(baseDate + jul * 86400000);
    return ret;
}

export function getUTCTimestamp(date) {
    let type = typeof (date);
    switch (type) {
        case "number":
            return date;
        case "string":
            return new Date(date + " UTC").getTime();
        default:
            return date.getTime();
    }
}

export const _mouseDown = function (event, g, context) {
    g.glGraph.mouseDown_(event);
    let callback = g.getOption("drawCallback");
    callback && callback(g, false);
    context.initializeMouseDown(event, g, context);
    if (event.button === 0) { // Left mouse
        window.Dygraph.startZoom(event, g, context);
    } else { // Middle and right mouse
        window.Dygraph.startPan(event, g, context);
    }
}

let _mouseMoveTimer = null;
export const _mouseMove = function (event, g, context) {
    if (_mouseMoveTimer) clearTimeout(_mouseMoveTimer);
    if (context.isPanning) {
        _mouseMoveTimer = setTimeout(function () {
            if (context.dragStartX === null || context.dragStartY === null) return;
            window.Dygraph.movePan(event, g, context);
        }, 1)
    }
    else if (context.isZooming) {
        window.Dygraph.moveZoom(event, g, context);
    }
}

export const _mouseUp = function (event, g, context) {
    if (context.isPanning) {
        window.Dygraph.endPan(event, g, context);
    } else if (context.isZooming) {
        window.Dygraph.endZoom(event, g, context);
    }

    g.glGraph.mouseUp_(event, (data) => {
        const pointClickCallback = g.getOption("pointClickCallback")
        pointClickCallback && pointClickCallback(event, data)
    })
}

// Take the offset of a mouse event on the window.dygraph canvas and
// convert it to a pair of percentages from the bottom left.
// (Not top left, bottom is where the lower value is.)
export const _offsetToPercentage = function (g, offsetX, offsetY) {
    // This is calculating the pixel offset of the leftmost date.
    let xOffset = g.toDomCoords(g.xAxisRange()[0], null)[0];
    try {
        var yar0 = g.yAxisRange(0);
    } catch (error) {
        return [0, 0]
    }

    // This is calculating the pixel of the higest value. (Top pixel)
    let yOffset = g.toDomCoords(null, yar0[1])[1];

    // x y w and h are relative to the corner of the drawing area,
    // so that the upper corner of the drawing area is (0, 0).
    let x = offsetX - xOffset;
    let y = offsetY - yOffset;

    // This is computing the rightmost pixel, effectively defining the
    // width.
    let w = g.toDomCoords(g.xAxisRange()[1], null)[0] - xOffset;

    // This is computing the lowest pixel, effectively defining the height.
    let h = g.toDomCoords(null, yar0[0])[1] - yOffset;

    // Percentage from the left.
    let xPct = w === 0 ? 0 : (x / w);
    // Percentage from the top.
    let yPct = h === 0 ? 0 : (y / h);

    // The (1-) part below changes it from "% distance down from the top"
    // to "% distance up from the bottom".
    return [xPct, (1 - yPct)];
}

export const _doubleClick = function (event, g) {
    // Return if no channel is plotting
    if (g.colorsMap_ && Object.keys(g.colorsMap_).length === 0) {
        return
    }
    // Reset view
    g.updateOptions({
        dateWindow: null,   // X
        valueRange: null    // Y
    });
}

export const _mouseClick = function (event, g, context) {
    g.clearSelection();
    event.preventDefault();
}

// maxRange = 2 days in ms
export const _mouseScroll = function (event, g, context, zoomScale = 30, maxRange = null) {
    event.preventDefault();

    let delta = Math.min(Math.max(event.deltaY, -360), 360) // Delta is in [-360, 360]
    let normal = event.detail ? event.detail : -1 * delta / zoomScale;
    let percentage = normal / 50;

    let offsetX = event.offsetX;
    let offsetY = event.offsetY
    if (!(event.offsetX && event.offsetY)) {
        offsetX = event.offsetX - (event.target).offsetLeft;
        offsetY = event.offsetY - (event.target).offsetTop;
    }

    let percentages = _offsetToPercentage(g, offsetX, offsetY);
    let xPct = percentages[0];
    let yPct = percentages[1];

    let zoomY = event.shiftKey || event.ctrlKey;
    if (!zoomY) {
        window.Dygraph.is_zooming_ = true;
    }
    _zoomChart(g, percentage, xPct, yPct, zoomY, maxRange);
    g.mouseOver_(event);
    window.Dygraph.is_zooming_ = false;

    let zoomCallback = g.getOption("zoomCallback");
    if (zoomCallback) {
        let xRange = g.xAxisRange();
        let yRange = g.yAxisRange();
        zoomCallback(xRange[0], xRange[1], yRange);
    }
}
// Adjusts [x, y] toward each other by zoomInPercentage%
// Split it so the left/bottom axis gets xBias/yBias of that change and
// tight/top gets (1-xBias)/(1-yBias) of that change.
//
// If a bias is missing it splits it down the middle.
export const _zoomChart = function (g, zoomInPercentage, xBias, yBias, isZoomY, maxRange) {
    xBias = xBias || 0.5;
    yBias = yBias || 0.5;
    isZoomY = !!isZoomY;
    let adjustAxis = function (axis, zoomInPercentage, bias) {
        let delta = axis[1] - axis[0];
        let increment = delta * zoomInPercentage;
        let foo = [increment * bias, increment * (1 - bias)];
        return [axis[0] + foo[0], axis[1] - foo[1]];
    }
    if (isZoomY) {
        let yRange = g.yAxisRange();
        let newYRange = adjustAxis(yRange, zoomInPercentage, yBias);
        g.updateOptions({ valueRange: newYRange })
    } else {
        let gRange = g.xAxisRange();
        let newRange = adjustAxis(gRange, zoomInPercentage, xBias)
        if (maxRange !== null && (newRange[1] - newRange[0] > maxRange)) {
            let startLength = Math.abs(newRange[0] - gRange[0]);
            let endLength = Math.abs(newRange[1] - gRange[1]);
            let increase = maxRange - gRange[1] + gRange[0];
            newRange = [gRange[0] - increase * startLength / (startLength + endLength), gRange[1] + increase * endLength / (startLength + endLength)]
        }
        g.updateOptions({
            dateWindow: newRange,
        });
    }
}

let graphSync = null;
export const syncGraphs = (graphList, doSync = true) => {
    detachGraphs();
    if (!doSync) return;
    if (graphList.length > 1) {
        try {
            graphSync = window.Dygraph.synchronize(graphList, {
                zoom: true,
                range: false
            });
        } catch (error) {
            console.log(error)
        }
    }
}

const detachGraphs = () => {
    if (graphSync) {
        graphSync.detach();
        graphSync = null;
    };
}
